<template>
  <div class="login">
        <div class="login_title">
          <h3>XYZ</h3>
        </div>
        <div class="login_form">
            <el-form>
                <el-form-item label="用户名">
                    <el-input type="text"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input type="password"></el-input>
                </el-form-item>
                <el-form-item  label="">
                    <el-button @click="login_in" type="primary">确认</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>

export default {
    props:{

    },
    data(){
        return{

        }
    },
    methods:{
        login_in(){
            let token;
            let that = this;
            console.log("login_in");
            this.axios.get('/static/user.json').then(function(response){
                console.log(response);
                console.log(response.data.user[0].token);
                console.log(response.data.user[0].name);
                console.log(response.data.user[0].password);
                token = response.data.user[0].token;

                that.$store.commit('set_token', token);
                that.$router.push('/home');
                console.log("token: "+that.$store.state.token);
            })

        }
    },
    computed:{

    },
    mounted(){

    }
}
</script>

<style lang="scss" scoped>

    .login{
        width: 100%;
        min-width: 200px;
        // background: url('/static/picture/dashboard.png') no-repeat center center;
        .login_title{
            text-align:center;
            margin:60px;
            padding: 0;
            // color: #409eff;
            // background-color: #f3f5f7;
        }
        .login_form{
            width:350px;
            min-width: 200px;
            margin:auto;
            padding: 20px 60px;
            border-radius: 10px;
            background-color: #f3f5f7;
        }
    }

</style>



